<script>
import { state, setState, onStateChange, Event } from './store.js'
import { loadPage } from './mainpanel.svelte'
let namespace = ''
let username = $state.user.username
function render(){
    let panel = jQuery('.LeftPanel')
    if (w2ui['LeftPanel']) return w2ui['LeftPanel']
    panel.w2sidebar({
        name: 'LeftPanel',
        nodes: [
            { id: 'settings', text: 'Settingsss', img: 'icon-folder', expanded: true, group: true,
                nodes: [ 
                    { id: 'overview', text: 'Overview', img:'icon-folder' }
                    ,{ id: 'golang', text: 'Go', img: 'icon-folder', 
                        nodes: [
                            { id: 'level-2-1-1', text: 'Level 2.1.1', icon: 'fa-star-empty' },
                        ]}
                    ,{ id: 'python', text: 'Python', img: 'icon-folder', 
                        nodes: [
                            { id: 'level-2-1-3', text: 'Level 2.1.3', icon: 'fa-star-empty' }
                        ]}
                ]
            }
            ,{ id: 'request-handlers', text: 'Request Handlers', img: 'icon-folder', expanded: true, group: true,
              nodes: [
                { id: 'getpost', text: 'Get and Post', img: 'icon-folder', expanded: true,
                nodes: [ 
                    { id: 'staticfile', text: 'Static File', icon: 'fa-home',href:'page/staticfile.html' },
                    { id: 'getpost2', text: 'Downlaod', icon: 'fa-home' ,href:'page/download.html'},
                    { id: 'getpost3', text: 'Dynamic Content by Get', icon: 'fa-home' },
                    { id: 'getpost4', text: 'Dynamic Content by Post', icon: 'fa-home' },
                    ]
                },
                { id: 'fileupload', text: 'File Upload', img: 'icon-folder', expanded: true, 
                nodes: [ 
                    { id: 'fileupload1', text: 'Upload File', icon: 'fa-star-empty' }
                    ]
                },
                { id: 'websocket', text: 'Websocket', img: 'icon-folder', expanded: true, 
                nodes: [ 
                    { id: 'Websocket-1', text: 'Level 1.3', icon: 'fa-star-empty' }
                    ]
                },
              ]
            }
            ,{ id: 'branches', text: 'Tree and Branches', img: 'icon-folder', expanded: true, group: true,
              nodes: [
                { id: 'branchesgeneral', text: 'What\'s Tree and Branches', icon: 'fa-home' },                  
                { id: 'gobranches', text: 'Branches in Go', img: 'icon-folder', expanded: true,
                nodes: [ 
                    { id: 'gobranches1', text: 'Settings', icon: 'fa-home' },
                    ]
                },
                { id: 'pythonbranches', text: 'Branches in Python', img: 'icon-folder', expanded: true, 
                nodes: [ 
                    { id: 'pythonbranches1', text: 'Settings', icon: 'fa-star-empty' }
                    ]
                },
              ]
            }

        ]
        ,onClick:function(evt){
            if (evt.node.href) {
                //Event.fire('mainpanel',{do:'set-url', url:evt.node.href})
                loadPage(evt.node.href)
            }
        }
    });    
}

onStateChange(evt =>{
    switch(evt.name){
        case 'change':
            switch (evt.key){
                case 'user':
                    username = $state.user.username
                    render()
            }
            break
    }
})
</script>
<style>
.LeftPanel{
    width:100%;
    height:100%;
}
</style>

<div class="LeftPanel"/>