
<script>
import { onMount } from 'svelte';
import { state, setState, onStateChange } from './store.js'

let username = 'guest'
function doLogin(username,password,callback){
    let data
    if (username){
        data = {username:username,password:password}
    }else{
        data = {}
    }
    jQuery.getJSON($state.loginUrl,data, function(response){
        if (response.username)  {
            setState('user',{username:response.username})
            callback(true,response)           
        }
        else {
            username = 'guest'
            callback(false)
        }
    })

}
onMount(async ()=>{
    doLogin(null,null,function(success, response){
        if (success) username = response.username
    })
})
function logout(){
    w2confirm(
        'Are you sure to logout?',
        function(yes){
            if (yes != 'Yes') return
            jQuery.get($state.logoutUrl,function(response){
                username = 'guest'
                setState('user',{username:'guest'})
            })
        }
    )
}
function login(){
    w2popup.open({
        title:'Login',
        body:jQuery('.loginform-template .loginform').clone(),
        buttons:'<button class="w2ui-btn login">Login</button>'
    })
    jQuery('#w2ui-popup button.login').on('click',function(){
        let name = jQuery('#w2ui-popup input.username').val()
        let password = jQuery('#w2ui-popup input.password').val()
        if (name && password){
            doLogin(name,password,function(success,response){
                if (success) {
                    username = response.username
                    w2popup.close()
                }
                else jQuery('#w2ui-popup .message').html('try again')
            })
        }
    })
}
</script>
<style>
.loginform{

}
.loginform input{
    display: block;
    padding: 10px;
}
</style>

{#if username=='guest'}
    <a  href="javascript:void(0);" on:click="{login}"><span>Login</span></a>
{:else}
    <a  href="javascript:void(0);" on:click="{logout}"><span title="{username}">Logout</span></a>
{/if}

<div style="display:none" class="loginform-template">
    <div class="loginform w2ui-center">
        <div class="message">&nbsp;</div>
        <input class="username" placeholder="username"/>
        <input class="password" type="password"/>
    </div>
</div>